@font-face {
    font-family: Roboto;
    src: url(/mvdb-api-react-build/static/media/Roboto-Light.88823c20.ttf);
}

.header{
    background-color: #000;
    display: block;
    padding-left: 16px;
    font-family: Roboto, serif;
}

.header-name {
    position: relative;
    padding-left: 10px;
    margin-top: 20px;
    display: inline-block;
    color: #fff;
    font-family: Roboto, sans-serif;
}


.genre-form{
    position: relative;
    font-family: Roboto, sans-serif;
    font-size: 14px;
    background-color: #000;
    bottom: 3px;
    color: white;
    width: 400px;
    height: 31px;
    z-index: 3;
}

.genre-button {
    position: relative;
    font-family: Roboto, sans-serif;
    font-size: 14px;
    background-color: #000;
    color: white;
    border-color: black;
    border-width: 3px;
    width: 60px;
    height: 25px;
    bottom: 3px;
}


.no-data-container {
    margin-left: 47%;
    position: absolute;
    top: 47%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}


.close {
    position: absolute;
    right: 20px;
    top: 5px;
    width: 32px;
    height: 32px;
    opacity: 0.3;

}
.close:hover {
    opacity: 1;
}
.close:before, .close:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 33px;
    width: 2px;
    background-color: #333;
}
.close:before {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.close:after {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

td {
  padding: 35px 0 0 0;
  text-align: left;

}


.data_table{
  margin: 4% 15%;
  border-top: 1px solid black;
  padding: 40px 10px;
}

.elem_poster{
  position: relative;
  text-align: center;
  vertical-align: baseline;
  margin-right: 50px;
  width: 250px;
  margin-bottom: 40px;
  margin-left: 25px;
  z-index: 1;
}

.elem_title{
  font-size: 20px;
  position: relative;
  text-align: left;
  vertical-align: top;
  width: 250px;
  padding-left: 30px;
  padding-top: 5px;
  top: -190px;
}

.elem_rating{
  position: relative;
  text-align: center;
  width: 100px;
  top: -120px;
  left: 10px;
}

.elem_genres{
  position: relative;
  text-align: left;
  vertical-align: bottom;
  top: -200px;
  left: 30px;
  margin-bottom: -120px;
}

.elem_ticket{
  position: relative;
  top: -420px;
  z-index: 2;
  width: 150px;
  left: 8px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@font-face {
  font-family: Roboto;
  src: url(/mvdb-api-react-build/static/media/Roboto-Light.88823c20.ttf);
}

html {
  background-color: #FAF7F2;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  font-weight: 400;
}


